// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from "axios";
import iView from 'iview';
import store from '@/store/store.js'
import storage from './utils/storage.js'
import leftMenuIndexFun from './utils/getLeftMenuIndex.js'
// import { MenuItem} from 'element-ui';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import 'iview/dist/styles/iview.css';
import '@/basecss/base.css'
import 'babel-polyfill'
Vue.use(iView);
// Vue.use(MenuItem)
Vue.use(ElementUI)
Vue.prototype.axios = axios
Vue.prototype.storage = storage
Vue.prototype.leftMenuIndexFun = leftMenuIndexFun
Vue.config.productionTip = false
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
axios.defaults.baseURL = "/api"
/* eslint-disable no-new */
var app = new Vue({
	el: '#app',
	router,
	store,
	storage,
	leftMenuIndexFun,
	components: { App },
	template: '<App/>'
})
//解决<td>遗失
Vue.directive('tdloss', {
	inserted: function (el, binding, vnode) {
		let code = binding.value.code;
		let list = binding.value.list;
		list.forEach(element => {
			if (element.levelCode == code) {
				el.innerHTML = element.levelName;
			}
		});
	},
	componentUpdated: function (el, binding, vnode) {
		let code = binding.value.code;
		let list = binding.value.list;
		list.forEach(element => {
			if (element.levelCode == code) {
				el.innerHTML = element.levelName;
			}
		});
	}
})
Vue.directive('authcontrol', {
	inserted: function (el, binding, vnode) {
		let menuCode = binding.value.menuCode;
		let eventBody = binding.value.eventBody;//事件体（实现事件和展示剥离）
		let menuCodes = binding.value.menuCodes;//弹窗的menuCode集
		let authCodes = menuCodes == null?app.$store.state.authCodes:menuCodes;
		let exist = false;
		authCodes.forEach(element => {
			if (element.menuCode == menuCode) {
				exist = true;
			}
		});
		if (!exist){
			if (eventBody == null)
				el.style.visibility="hidden";
		}else {
			el.style.visibility="visible";
			if (eventBody != null)
				el.addEventListener('click',eventBody);
		}
	}
})
//拦截器start-------------
axios.interceptors.request.use(
	config => {
		return config;
	},
	err => {
		return Promise.reject(err);
	});
axios.interceptors.response.use(
	response => {
		return response.data;
	},
	error => {
		if (error) {
			switch (error.response.status) {
				case 401:
					error.message = 'token无效'
					this.$router.push({ name: "signInTo" });
					break;
				case 404:
					error.message = '请求错误,未找到该资源'
					break;
				case 408:
					error.message = '请求超时'
					break;
				case 500:
					error.message = '服务器繁忙'
					break;
				case 501:
					error.message = '网络未实现'
					break;
				case 502:
					error.message = '网络错误'
					break;
				case 503:
					error.message = '服务不可用'
					break;
				case 504:
					error.message = '网络超时'
					break;
				default:
					error.message = '发生未知错误'
					break;
			}
			app.$Message.error(error.message);
		}
		return Promise.reject(error.response.data)   // 返回接口返回的错误信息
	});
//拦截器end-------------
router.beforeEach((to, from, next) => {
	let cookieVal = getCookie('LtpaToken2');
	let cookieValAml = getCookie('AmlLtpaToken');

	let  alienUrls=app.$store.state.alienUrls;
	let menuCode = to.query.menuCode == null?to.meta.menuCode:to.query.menuCode;
	if(cookieVal || cookieValAml){
		if (to.path != '/signIn') {
			let menuBox = app.$store.state.menuBox;
			let leftMenuBoxBody = app.$store.state.leftMenuBoxBody;
			let topmenuboxIndex = app.leftMenuIndexFun.getTopMenuIndex(menuCode);//监控顶部菜单栏标
			let leftmenuboxIndex = "";
			if (to.path == '/workCenter'){
				app.$store.commit("menuBoxIndex", {
				menuBoxIndex: "1"
				});
				app.storage.set("menuBoxIndex", "1"); 
				next();
				return;
			}
			if (menuCode == null) {
				next();
				return;
			}
			// if (alienUrls[menuCode]){
			// 	topmenuboxIndex = alienUrls[menuCode]["topmenuboxIndex"];
			// 	leftmenuboxIndex = alienUrls[menuCode]["leftmenuboxIndex"];
			// 	app.$store.commit("menuBoxIndex", {
			// 		menuBoxIndex: topmenuboxIndex
			// 	});
			// 	app.storage.set("leftMenuBox", leftmenuboxIndex);
			// 	app.$store.commit("leftMenuBox", {
			// 		leftMenuBox: leftmenuboxIndex
			// 	});
			// 	next();
			// 	return;
			// }
			if (leftMenuBoxBody.length == 0 || topmenuboxIndex == ""){
				let params = new FormData();
				params.append("menuCode", menuCode);
				app.axios
				.post(`/system/menu/getLeftMenuByMenuCode`,params, {
					headers: { "Content-Type": "multipart/form-data"}
				})
				.then(response => {
					if (response.code == "200") {
						if (!response.data.listMenu || response.data.listMenu.length == 0){
							app.$Message.warning("暂未开通该资源权限");
							next(false);
							return;
						}
						app.storage.set("leftMenuBoxBody", response.data.listMenu);
						app.$store.commit("leftMenuBoxBody", {
							leftMenuBoxBody: response.data.listMenu
						});
						topmenuboxIndex = response.data.listMenu[0].sysNo;
						leftmenuboxIndex = app.leftMenuIndexFun.getLeftMenuIndex(response.data.supMenuCode == ""?menuCode:response.data.supMenuCode);
						app.$store.commit("menuBoxIndex", {
							menuBoxIndex: topmenuboxIndex
						});
						app.storage.set("leftMenuBox", leftmenuboxIndex);
						app.$store.commit("leftMenuBox", {
							leftMenuBox: leftmenuboxIndex
						});
						app.axios
						.post(`/system/menu/getMenuSon?menuCode=${menuCode}`)
						.then(response2 => {
							if (response2.code == "200") {
								app.storage.set("authCodes", response2.data);
								app.$store.commit("authCodes", {
									authCodes: response2.data
								});
								next();
							}
						})
						.catch(error => { });
					}
				})
				.catch(error => { });
			}else {
				leftmenuboxIndex = app.leftMenuIndexFun.getLeftMenuIndex(menuCode);
				app.$store.commit("menuBoxIndex", {
					menuBoxIndex: topmenuboxIndex
				});
				app.$store.commit("leftMenuBox", {
					leftMenuBox: leftmenuboxIndex
				});
				app.axios
				.post(`/system/menu/getMenuSon?menuCode=${menuCode}`)
				.then(response2 => {
					if (response2.code == "200") {
						app.storage.set("authCodes", response2.data);
						app.$store.commit("authCodes", {
							authCodes: response2.data
						});
						next();
					}
				})
				.catch(error => { });
			}
		} 
    } else {
		if (to.path === '/signIn') { //这就是跳出循环的关键
			next()
		} else {
			next('/signIn')
		}
	}


})
function getCookie(cookieName) {
	var strCookie = document.cookie;
	var arrCookie = strCookie.split("; ");
	for (var i = 0; i < arrCookie.length; i++) {
		var arr = arrCookie[i].split("=");
		if (cookieName == arr[0]) {
			return arr[1];
		}
	}
	return "";
}


