import Vue from 'vue'
import Vuex from 'vuex'
import storage from '../utils/storage.js'
// import vuexAlong from 'vuex-along'
Vue.use(Vuex)
Vue.use(storage)
const store=new Vuex.Store({
    state:{
        menuBox:[],//顶级菜单
        alienUrls: {
            //"100000": {topmenuboxIndex:null, leftmenuboxIndex: null, funCode: "1"}, //提取数据详情-成功数量/错误数量按钮
        },
        firstLoginFlag:false,
        clientViewIndex: "1",
        clientId: "",
        type: "",
        authCodes: [],//可操作code集
        leftMenuBox:'',//左侧菜单索引
        leftMenuBoxBody:[],//左侧菜单
        menuBoxIndex:1,//顶级菜单索引
        username:''//用户值
    },
    mutations:{
        menuBox (state, payload){
            state.menuBox = payload.menuBox;
        },
        firstLoginFlag (state, payload){
            state.firstLoginFlag = payload.firstLoginFlag;
        },
        clientViewIndex (state, payload){
            state.clientViewIndex = payload.clientViewIndex;
        },
        clientId (state, payload){
            state.clientId = payload.clientId;
        },
        type (state, payload){
            state.type = payload.type;
        },
        leftMenuBox (state, payload){
            state.leftMenuBox = payload.leftMenuBox;
        },
        username (state, payload){
            state.username = payload.username;
        },
        menuBoxIndex (state, payload){
            state.menuBoxIndex = payload.menuBoxIndex;
            state.menuBox.forEach(e => {
                if (e.sysNo == state.menuBoxIndex)
                    e.flag = true;
                else e.flag = false;
            });
        },
        leftMenuBoxBody(state, payload){
            state.leftMenuBoxBody = payload.leftMenuBoxBody;
        },
        authCodes(state, payload){
            state.authCodes = payload.authCodes;
        }
    },
})
// 页面刷新时，会从sessionStorage中重新读取并赋值
for(let key in store.state) {
    storage.get(key) ? store.state[key] = storage.get(key) : false;
}

export default store;


