//操作封装本地存储方法
import store from '@/store/store.js'
let leftMenuIndexFun = {
    getLeftMenuIndex(parmarsVal) {
        let indexNub = '';
        var MenuArr = [
        ];
        MenuArr = store.state.leftMenuBoxBody;
        MenuArr.forEach((element, index) => {
            if (element.menuCode == parmarsVal) {
                indexNub = element.menuCode;
                return
            } else if (element.arr && element.arr.length > 0) {
                element.arr.forEach((element2, index2) => {
                    if (element2.menuCode == parmarsVal) {
                        indexNub = element2.menuCode;
                        return
                    } else if (element2.arr && element2.arr.length > 0) {
                        element2.arr.forEach((element3, index3) => {
                            if (element3.menuCode == parmarsVal) {
                                indexNub = element3.menuCode;
                                return;
                            } else {
                                return
                            }
                        })
                    }
                })
            }
        })
        return indexNub
    },
    getTopMenuIndex(parmarsVal){
        let indexNub = '';
        var MenuArr = [
        ];
        MenuArr = store.state.leftMenuBoxBody;
        MenuArr.forEach(element=>{
            if(element.menuCode==parmarsVal){
                indexNub=element.sysNo;
                return
            }else if(element.arr && element.arr.length > 0){
                element.arr.forEach(element2=>{
                    if(element2.menuCode==parmarsVal){
                        indexNub=element.sysNo;
                        return
                    }else if(element2.arr && element2.arr.length > 0){
                        element2.arr.forEach(element3=>{
                            if(element3.menuCode==parmarsVal){
                                indexNub=element.sysNo;
                                return
                            }else{
                                return
                            }
                        })
                    }
                })
            }
        })
        return indexNub
    },

}
export default leftMenuIndexFun;


