<template>
  <div id="app" style>
    <router-view />
  </div>
</template>
<script>
export default {
  created() {
    let cookieVal = this.getCokies("LtpaToken2");
    let cookieValAml = this.getCokies("AmlLtpaToken");
    if (cookieVal || cookieValAml) {
      if (!this.$store.state.firstLoginFlag) {
          let menuCode = this.getUrlParams(document.URL, "menuCode");
          this.axios
            .post(`/system/getSysUserByToken`)
            .then(response => {
              if (response.data.code == "200") {
                this.storage.set("username", response.data.data.username);
                this.$store.commit("username", {
                  username: response.data.data.username
                });
              }
            })
            .catch(error => {});
            this.getTopMenu(menuCode);
      }
    } else {
      this.$router.push({ name: "signInTo" });
    }
  },
  watch: {
    $route: {
      handler(to, form) {},
      immediate: true
    }
  },
  methods: {
    getCokies(cookieName) {
      var strCookie = document.cookie;
      var arrCookie = strCookie.split("; ");
      for (var i = 0; i < arrCookie.length; i++) {
        var arr = arrCookie[i].split("=");
        if (cookieName == arr[0]) {
          return arr[1];
        }
      }
      return "";
    },
    getQueryString(url) {
      let arr = url.split("?");
      if (arr.length > 0) {
        let documentArr = arr[arr.length - 1].split("=");
        if (documentArr[0] == "documentId") {
          return true;
        }
      }
      return false;
    },
    getUrlParams(url, name) {
      if (url.split("?").length < 2) return "";
      let arr = url.split("?")[1].split("&");
      let obj = {};
      arr.forEach(element => {
        obj[element.split("=")[0]] = element.split("=")[1];
      });
      return obj[name];
    },
    async getTopMenu(menuCode) {
      let menurandomBox = [];
      let response = await this.axios.post("/system/menu/getTopMenu");
      if (response.data.code == "200") {
        menurandomBox = response.data.data;
        menurandomBox.forEach(element => {
          element.flag2 = false;
          if (element.sysNo == "1") element.flag = true;
          else element.flag = false;
        });
        this.storage.set("menuBox", menurandomBox);
        this.$store.commit("menuBox", {
          menuBox: menurandomBox
        });
        if (menuCode)
            this.getLeftMenu(menuCode);
        else {
          this.$router.push({ name: "workCenter" });
        }
      }
    },
    getLeftMenu(menuCode) {
      let params = new FormData();
      this.axios
        .post(`/system/menu/getMenuSon?menuCode=${menuCode}`)
        .then(response2 => {
          if (response2.code == "200") {
            this.storage.set("authCodes", response2.data);
            this.$store.commit("authCodes", {
              authCodes: response2.data
            });
            this.storage.set("firstLoginFlag", true);
            this.$store.commit("firstLoginFlag", {
              firstLoginFlag: true
            });
          }
        })
        .catch(error => {});
      params.append("menuCode", menuCode);
      this.axios
        .post(`/system/menu/getLeftMenuByMenuCode`, params, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            this.storage.set("leftMenuBoxBody", response.data.listMenu);
            this.$store.commit("leftMenuBoxBody", {
              leftMenuBoxBody: response.data.listMenu
            });
            let topmenuboxIndex = response.data.listMenu[0].sysNo;
            let leftmenuboxIndex = this.leftMenuIndexFun.getLeftMenuIndex(
              response.data.supMenuCode == ""
                ? menuCode
                : response.data.supMenuCode
            );
            this.$store.commit("menuBoxIndex", {
              menuBoxIndex: topmenuboxIndex
            });
            this.storage.set("leftMenuBox", leftmenuboxIndex);
            this.$store.commit("leftMenuBox", {
              leftMenuBox: leftmenuboxIndex
            });
          }
        })
        .catch(error => {});
    }
  },
  name: "App"
};
</script>
<style>
body {
  margin: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /* min-width: 1480px; */
}
</style>



